class App extends Component {
  constructor(props){
        super(props);
        this.state = {
            date : demo_date,
            date_top_charter:demo_date,
            date_chareter_list: demo_date,
            demo:{},
            startPreload: false,
            startLoading: false,
            formData:null
        };
        this._filtredDate = this._filtredDate.bind(this);
    }
  
   async _filtredDate(from){
        let date = {
            login:'123',
            password:'123'
        };
        
        this.setState({startLoading:true,startPreload:true});
        this.setState({date_chareter_list:this.state.date.filter(item=>item.from == from)});
   }
   _templateDate(){
       let template;
        if(this.state.startLoading == true){
            this.state.startPreload == true ?  template = <Preload/> : template = <div>not hello</div>
        }
        else {
          return <div>Hello world</div>
        }
        return template;
   }
   _selectFormaDate = (data) =>{
       this.setState({formData:data});
       
   }
  render() {
    if(this.state.formData){
        return ( 
            <div className="AppFormTravelSoft">    
              <CharterForm  callback={this._selectFormaDate}/>
              {
                   <CharterList data = {this.state.formData} />  
              }
          </div>
          );
    }
    else{
        return ( 
            <div className="AppFormTravelSoft">    
              <CharterForm  callback={this._selectFormaDate}/>
          </div>
          );
    }
    
  }
}

export default App;


