import React from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import withMobileDialog from '@material-ui/core/withMobileDialog';
import MuiPickersUtilsProvider from 'material-ui-pickers/utils/MuiPickersUtilsProvider';
import MomentUtils from 'material-ui-pickers/utils/moment-utils';
import TEXTFIELD from './textField.jsx';
import 'moment/locale/ru';
import moment from 'moment';

class DIALOGDATE extends React.Component {
  state = {
    open: false,
    disabled: this.props.disabled,
    classes: this.props.classes,
    error: this.props.error
  };
  componentWillReceiveProps(nextProps){
    this.setState({disabled:nextProps.disabled, error:nextProps.error})
   }
  handleClickOpen = () => {
    if(this.state.disabled != true){
      this.setState({ open: true});
      
    }
    
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  render() {
    const { fullScreen } = this.props;
    
    return (
      <React.Fragment>
        
        <div className={this.props.classes} disabled={this.props.disabled} onClick={this.handleClickOpen}>
           <TEXTFIELD onClick={this.handleClickOpen} defaultValue={this.props.defaultValue} label={this.props.label} data={this.props.data} error={this.state.error}/>
          
        </div>
       
        <Dialog
          fullScreen={fullScreen}
          open={this.state.open}
          onClose={this.handleClose}
          aria-labelledby="responsive-dialog-title"
        >
          <DialogTitle id="responsive-dialog-title">{"Use Google's location service?"}</DialogTitle>
          <DialogContent>
          <MuiPickersUtilsProvider utils={MomentUtils} locale={'ru'}>
                {this.props.Node}
             </MuiPickersUtilsProvider>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              Закрыть
            </Button>
            <Button onClick={this.handleClose} color="primary" className="submit" autoFocus>
              Выбрать
            </Button>
          </DialogActions>
        </Dialog>
        </React.Fragment>
    );
  }
}



export default withMobileDialog()(DIALOGDATE);


