
import React, { Component } from 'react';
import _ from 'lodash';
import './style.scss';
import Button from '@material-ui/core/Button';


export default class RANGEPICKER extends Component {
  constructor(props){
    super(props);
   
    this.state = {
        date:this.props.date,
        min: 0,
        max: 0,
      };

  }

componentDidUpdate(){
    
    let td = document.querySelectorAll('.range-picker td');
    if(this.state.min != 0){
      td[this.state.min-1].classList.add('active');
    }
    if(this.state.max != 0){
     
      td[this.state.max-1].classList.add('active');
      if(this.state.max>this.state.min){
         for(let i=this.state.min; i<this.state.max-1;i++){
          td[i].classList.add('range');
        }
      }
      else{
        let buf = this.state.max;
        this.setState({max:this.state.min,min:buf});
        for(let i=this.state.min; i<this.state.max-1;i++){
          td[i].classList.add('range');
        }
      }
     
    }
  }
  
  _OnSelect = (event) =>{
    
    let current = parseInt(event.currentTarget.innerText);
    if(this.state.min == 0){
      this.setState({min: current});
    }
    else if(this.state.min != 0 & this.state.max !=0){
      
      this.setState({min:0,max:0});
    }
    else {
      this.setState({max:current});
      this.props.callback([this.state.min,current]);
    }
  }
 


  _CreateTable = () =>{
      let arr = [];
      for(let i=4; i<21; i++){
        arr[i-1] = i;
      }
      return _.chunk(arr,5);
  }
  


    
  

  
  render() {
    let template = this._CreateTable.call(this);
    
    return (
         
              <table className="range-picker">
                <tbody>
                  {
                    template.map((item,index)=>(
                      <tr key={Math.random()}>
                       {
                         item.map(elem=><td  key={Math.random()} onClick={this._OnSelect}>{elem}</td>)
                       }
                      </tr>
                    ))
                  }
                </tbody>
              </table>

                
          
           
    
    );
  }
}





