import React from 'react';
import TABLE from './table';
import HEADER_CONTAINER from './header_container';
//import 'bootstrap';


    
export default function container (props) {
  return (
    <div className="top-item-charter-list">
          <HEADER_CONTAINER name={props.name} />
          <div className="body-charter-list">
          { 
                props.date.map((element,index)=>{
                return <TABLE key={Math.random()} date={element} callback = {props.callback} />
              }
            )}
            </div>
    </div>
      
    
  );
}


