
import React, { Component } from 'react'
import CONTAINER from './container';
import _ from 'lodash';
import './style.scss';

    
export default class TopCharterList extends Component {
  constructor(props){
    super(props);
    this.keysDate = _.uniq(this.props.date.map(item=>item.from));
    this.state = {
       // date_notFilter:this.props.date,
        date:this._GroupDate(this.props.date).map(item=>_.chunk(item,5))};
    

  }
  _GroupDate(date){
    let arr = [];
    for(let i=0; i<this.keysDate.length; i++){
        arr[i] = date.filter((item)=>item.from ==this.keysDate[i]);
        arr[i] = _.sortBy(arr[i],'price');
    }
    return arr;
   }
  render() {
    return (
      <React.Fragment>
     
            <div className="top-charter-list">
             
            { 
              this.state.date.map((element,index)=>{
                return <CONTAINER key={Math.random()} date={element}  name = {this.keysDate[index]} callback = {this.props.callback}/>
              }
            )}
            </div>
            </React.Fragment>
    
    );
  }
}





