import React from 'react'
import BUTTON from './button';
import Arrows from '@material-ui/icons/CompareArrows.js';

    
export default function ROW ({date,callback}) {
  return (
    
      <tr>
        <td>{date.from}</td>
         <td><Arrows/></td>
        <td>{date.to}</td>
        <td><BUTTON date={date.price} callback = {callback} from={date.from}/></td>
      </tr>
    
  );
}


