import React from 'react';
import Button from '@material-ui/core/Button';
    
export default function BUTTON ({date}) {
  return (
      <div className="submit-charter-list">
        <span className="price"> {date} $ </span>
      <Button variant="contained">
        КУПИТЬ ОНЛАЙН
      </Button>
      </div>
  );
}


