import React from 'react'
import BUTTON from './button';
import PEOPLE from '@material-ui/icons/People';
import CHECK from '@material-ui/icons/Check';
    
export default function ITEMDOPINFO (props) {
  return (
    
    <div className="body-filter-charter-list">
    <ul>
        <li>
          <span> <PEOPLE/> </span>
          <span>{props.men} - взрослых, {props.young} - детей, {props.baby} - младенцев</span>
        </li>
        <li>
          <span> <CHECK/> </span>
          <span>{props.info}</span>
        </li>
        <li>
          <span> <CHECK/> </span>
          <span>{props.bagaje}</span>
        </li>

    </ul>
    <BUTTON date = {props.price}/>
  </div>
    
  );
}



