import React from 'react';
import Tooltip from '@material-ui/core/Tooltip';



const INPUT = ({data,label,error}) =>(
  <Tooltip 
    title={"Извените но по вашему запросу нечего не найдно"} 
    placement="bottom-start"
    open={error}>
  <React.Fragment>
    
      <label>{label}</label>
      <div className="placeCode">MSQ</div>  
      <input readOnly placeholder="Старана прилета" value={data ? data : ''}/>
       
  </React.Fragment>
  </Tooltip>
  
)

export default INPUT;









