import React, { Component } from 'react'
import TEXTFIELD from './textField';
import AddIcon from '@material-ui/icons/Add';
import Remove from '@material-ui/icons/Remove';
import Button from '@material-ui/core/Button';

export default class Counter extends Component {
  state = {
    count:1
   
  }
  

  _Increment=()=>{
    let increment = parseInt(this.state.count);
    if(increment<7){
      increment++;
    }
  
    this.setState({count:increment});
    this.props.callback(increment);

  }
  _Dicrement=()=>{
    let dicrement = parseInt(this.state.count);
    if( dicrement>1){
      dicrement--;
      this.setState({count: dicrement--})
      this.props.callback(dicrement);
    }
    
  }


 


  render() {
   

    return (
      <React.Fragment>
         <Button variant="fab" color="primary" mini aria-label="Add"  onClick={this._Dicrement}>
          <Remove />
        </Button>
          <TEXTFIELD data={this.state.count}/>
         <Button variant="fab" color="primary" mini aria-label="Add"  onClick={this._Increment}>
          <AddIcon />
        </Button>
      </React.Fragment>
    );
  }
}


