import React from 'react';
import Button from '@material-ui/core/Button';
//import Icon from '@material-ui/core/Icon';
import PlayIcon from '@material-ui/icons/PlayArrow';

    
export default function BUTTON ({date,callback,from}) {
  return (
    <Button variant="contained" color="secondary" className="submit-top-charter" onClick = {()=>callback(from)}>
        {date}$
        <PlayIcon/>
    
  </Button>
  );
}


