import React from 'react'
import ROW from './row';
import PlaceIcon from '@material-ui/icons/Place';

    
export default function TABLE (props) {
  return (
      <table>
        <thead>
            <tr>
                <th colSpan="2">
                    <span><PlaceIcon className="position-icon"/></span>
                    <span>откуда</span>
                </th>
                <th colSpan="2">
                    <span><PlaceIcon className="position-icon"/></span>
                    <span>Куда</span>
                </th>
            </tr>
        </thead>
          <tbody>
            { 
              props.date.map((element,index)=>{
                return <ROW key={Math.random()} date={element} callback = {props.callback}/>
              }
            )}
          </tbody>
      </table>
    
  );
}


