
import React, { Component } from 'react'
import SELECT from './select';
import DATEPICKER from './date';
import DIALOGDATE from './dialogDate';
import RANGE from './rangepiceker';
import COUNTER from './counter';
import _ from 'lodash';
import './style.scss';
import 'moment/locale/ru';
import moment from 'moment';
import Button from '@material-ui/core/Button';
import 'react-day-picker/lib/style.css';
import PRELOAD from '../Preload';



export default class CharterForm extends Component {
  constructor(props){
    super(props);
    this.state = {
      data:null,
      disabled:true,
      fromPlace: null,
      AllDateFrom:null,
      SendDataFrom:null,
      charterList:null,
      toPlace:null,
      today: moment(new Date()).format('DD.MM.YYYY'),
      SelectFromPlace:'',
      SelectToPlace:null,
      date:null,
      disableDate: [],
      men:'',
      young:'',
      token:'',
      range:[6,12],
      loading:false,
      error:false
      }; 
    }
    componentWillMount(){   
      let date = {
        login: 'travelsoft',
        password:'hdyNb81Uf-q&51M'
    };
     this.setState({loading:true});
     fetch('http://online.abstour.by:88/ChartersWin64/api/Charter/GetToken', {
        method:"POST",
        headers: {"Content-Type":'application/json'},
        body: JSON.stringify(date)
      })
      .then(
        response=> response.json()
      )
      .then(
        response=>{
          this.setState({token:response.token})
          return response;
        }
      )
      .then(
        response=>{ 
          fetch('http://online.abstour.by:88/ChartersWin64/api/Charter/GetDepartureCities',{
          method:"POST",
          headers: {"Content-Type":'application/json'},
          body: JSON.stringify({token:this.state.token})
          })
          
        .then(
          response=>response.json()
        )
        .then(
          json =>{
            let defaultValue = _.find(json,(obj)=>obj.cityName === 'Минск');
            this.setState({SelectFromPlace:defaultValue.cityName});
            this.setState({fromPlace:json});
            
          }
        )
        return response}
      ).then(response=>{
        let body = JSON.stringify({token:this.state.token, cityFromKey : 448});
        fetch('http://online.abstour.by:88/ChartersWin64/api/Charter/GetArrivalCities',{
        method:"POST",
          headers: {"Content-Type":'application/json'},
          body: body
        })
        .then(response=>response.json())
        .then(json=>this.setState({toPlace:json,loading:false}));     
      })
      
    }
   
    

    _addDate = (data,key) =>{
     return _.uniq(data.map(item=>item[key]))
    }

    _selectFrom = (data) =>{
         
          if(data){
            this.setState({SelectFromPlace:data});
            let object = _.find(this.state.fromPlace,(obj)=>obj.cityName == data);
            let body = JSON.stringify({token:this.state.token, cityFromKey : object.cityKey});

          fetch('http://online.abstour.by:88/ChartersWin64/api/Charter/GetArrivalCities',{
            method:"POST",
              headers: {"Content-Type":'application/json'},
              body: body
            })
            .then(response=>response.json())
            .then(json=>this.setState({toPlace:json}))      
            }
    }
    _selectTo = (data) =>{
      if(data){
        this.setState({SelectToPlace:data});
        let from = _.find(this.state.fromPlace,(obj)=>obj.cityName == this.state.SelectFromPlace); 
        let to = _.find(this.state.toPlace,(obj)=>obj.countryName == data);
  
        let body = JSON.stringify({
          token:this.state.token,
          countryToKey: to.countryKey,
          cityFromKey : from.cityKey,
        });
  
        fetch('http://online.abstour.by:88/ChartersWin64/api/Charter/GetStraightFlightDates',{
          method:"POST",
            headers: {"Content-Type":'application/json'},
            body: body
          })
          .then(response=>response.json())
          .then(json=>{
            if(json.length != 0 && json){
              this.setState({
                            AllDateFrom:json,
                            disabled:false,
                            error: false,
                            today: moment(json[0]).format('DD.MM.YYYY')
                          });
              this._disableDate(json);
              }
            else{
                this.setState({disabled:true,error:true});
              }
            }
            
          )
      }
     
    }

    _selectDate = (data) =>{
      if(data){
        this.setState({date:data});
      }
      
    }
   
    _selectMen = (data) =>{
      this.setState({men:data});
    };
    _selectYoung = (data) =>{
      this.setState({young:data})
    };
    _selectRange = (data) =>{
      if(data){
        this.setState({range:data});
      }
      
        
    };
   
    
    _sortDate = (data) =>{
      if(data){
         let arr = data.filter(item=>{
          if(moment(this.state.date.from).format('YYYY-MM-DD') <= moment(item).format('YYYY-MM-DD') && (moment(this.state.date.to).format('YYYY-MM-DD') >= moment(item).format('YYYY-MM-DD'))){
           return item;
          }
        })
        return arr;
      }
    }

    _formatDateCalendarb = (data) => {
      if(data){
        let replace = _.replace(data, '"', '');
        replace = replace.split('.');
        if(parseInt(replace[1])>9){
          replace[1] = `0${replace[1]}`;
        }
        let newData = `${parseInt(replace[2])}-${parseInt(replace[1])}-${parseInt(replace[0])}`;
        return  String(moment(newData).format('YYYY-MM-DD'));
      }  
    };
    
    _dateRange = (data) =>{
      if(data){
        let arr = data.filter(item=>{
          if( moment(this._formatDateCalendarb(item)) >= moment(new Date()).add(this.state.range[0],'days') && moment(this._formatDateCalendarb(item)) <= moment(new Date()).add(this.state.range[1],'days')){
            return this._formatDateCalendarb(item);
          }
        })
        
        arr = arr.map(item=>this._formatDateCalendarb(item));
       
      }
    }; 
    _templateDataRange = () =>{
      if(this.state.filterDateBack.length>1){
        let lastindex = this.state.filterDateBack.length;
        return [new Date(this.state.filterDateBack[0]), new Date(this.state.filterDateBack[lastindex-1])];
      }
      else if(this.state.filterDateBack.length == 0){
        return new Date();
      }
      else return new Date(this.state.filterDateBack[0]);
    }
    
            
    _calendarbCreate = () =>{
      let arr = [];
      for(let i = 0; i<365; i++){
        arr[i]= moment(new Date()).subtract(1, 'months').add(i,'days').format('YYYY-MM-DD'); 
      }
      return arr;
    }

    _disableDate = (data) =>{
      let newDate = this._calendarbCreate();
      let date  = [];
        newDate.forEach((item,index)=>{
          if(!data.includes(item)){
            date.push(new Date(moment(item).format("YYYY,MM,DD")));
          }
        })
        this.setState({disableDate:date});
    }
    
    _Submit = () =>{
      let fromPlace = _.find(this.state.fromPlace,(obj)=>obj.cityName == this.state.SelectFromPlace); 
      let toPlace = _.find(this.state.toPlace,(obj)=>obj.countryName == this.state.SelectToPlace);
      let TourDates = this.state.date ? this._sortDate(this.state.AllDateFrom) : this.state.AllDateFrom;
      alert('начинаю поиск');
      let body = JSON.stringify({
        Token:this.state.token,
        SearchPeriodFilter:{
          CountryToKey : toPlace.countryKey,
          CityFromKey : fromPlace.cityKey,
          TourDates: TourDates,
          DurationFrom: parseInt(this.state.range[0]),
          DurationTo: parseInt(this.state.range[1]),
          RateISOCode:"USD",
          CharterClass:89
        }
        
      });
      
      
      fetch('http://online.abstour.by:88/ChartersWin64/api/Charter/GetPeriodFlights',{
        method:"POST",
          headers: {"Content-Type":'application/json'},
          body: body
        })
        .then(response=>{
         
          return response.json();
        })
        .then(resolve=>{
          
          if(resolve.length != 0){
            alert('поиск закончен успешно');
            this.props.callback(resolve);
          }
          else alert('поиск нечего не дал')
          
        })
       
       
        
      
    }
   
      
  render() {
    

    if(!this.state.loading){
      return (
        <div className="Inputter">
            <div className="topContainer">
            <DIALOGDATE key="From_place" 
                        disabled={false} 
                        label={'Откуда'}
                        classes =  {'item'}
                        defaultValue={'Минск'} 
                        data={this.state.SelectFromPlace} 
                        Node = {<SELECT key="from_place_select"   
                                        place ={this.state.fromPlace ? this._addDate(this.state.fromPlace,'cityName'): <PRELOAD/>} 
                                        callback ={this._selectFrom}/>}/>
               
              </div>
              <div className="bottomContainer">
              <DIALOGDATE key="to_place"
                        classes =  {this.state.error ? 'item error' : 'item'}   
                        disabled={false}
                        error = {this.state.error} 
                        label={'Куда'} 
                        defaultValue = {''} 
                        data={this.state.SelectToPlace} 
                        Node = {<SELECT   key="to_place_select"  
                                          place ={this.state.toPlace ? this._addDate(this.state.toPlace,'countryName'): this.props.place} 
                                          callback ={this._selectTo}/>}/>
            <DIALOGDATE key="date_from"
                        classes =  {this.state.disabled ? 'item disabled' : 'item'}     
                        disabled={this.state.disabled} 
                        label={'Дата вылета'} 
                        data={this.state.date ? this.state.date.to ? `c ${moment(this.state.date.from).format('DD.MM.YYYY')} по ${moment(this.state.date.to).format('DD.MM.YYYY')}`:`c ${moment(this.state.date.from).format('DD.MM.YYYY')}` : ` c ${this.state.today}`} 
                        Node = {  <DATEPICKER  disabledDays={this.state.disableDate} callback={this._selectDate}/> }/>
            <DIALOGDATE key="Range_date"
                        classes =  {this.state.disabled ? 'item disabled' : 'item'}    
                        disabled={this.state.to != '' ? false : true}  
                        label={'Ночей'} data={ this.state.range ? "с " + this.state.range[0] +" по "+ this.state.range[1] : 3} 
                        Node = {<RANGE   key="range_picker"  callback={this._selectRange}/>}/>
            
            
          
     
         
            <Button   variant="contained"  onClick={this._Submit} className="submit">
              Искать
            </Button>
            
            </div>
        </div>

);
    }
    else{
      return <PRELOAD/>
    }
   
  }
}

