import React from 'react'
import ITEM from './item';
import ITEMDOPINFO from './itemdopinfo';
import HEADERITEM from './headerItem';
import FLIGHTLAND from '@material-ui/icons/FlightLand';
import FLIGHTTAKEOFF from '@material-ui/icons/FlightTakeoff';
//Users/egor/Desktop/проекты/new-project/node_modules/@material-ui/icons/
export default function TABLE (props) {
  return (
      <React.Fragment>
        <div className="col-50">
            
            <HEADERITEM place = {props.date.cityFromName} date={props.date.dateTo}  component = {<FLIGHTTAKEOFF/>} />
            <ITEM from={props.date.cityFromName} to={props.date.countryToName} time = {props.date.charterTime} time_road = {props.date.time_road} company = {props.date.airlineName} idCharter = {props.date.id} />
            
        </div>
        <div className="col-50">
            <HEADERITEM place = {props.date.cityToName} date={props.date.dateBack} component = {<FLIGHTLAND/>} />
            <ITEM from={props.date.cityToName} to={props.date.cityFromName} time = {props.date.charterBackTime} time_road = {props.date.time_road} company = {props.date.airlineName} idCharter = {props.date.id} />
          
        </div>
        <div className="col-50">
           <ITEMDOPINFO men={props.date.men} young={props.date.young} baby={props.date.baby} price={props.date.cost} bagaje={props.date.bagaje} info={props.date.info}/>
        </div>
        </React.Fragment>


  );
}
